<template>
  <div>
    <div id="view" :style="'background: '+view.colors.ambient+';background: -moz-linear-gradient(0deg, '+view.colors.ambient+', '+view.colors.directional+');background: -webkit-linear-gradient(0deg, '+view.colors.ambient+', '+view.colors.directional+');background: linear-gradient(0deg, '+view.colors.ambient+', '+view.colors.directional+');'">
    </div>
    <div v-if="!view.isLoading">
      <tooltip />
      <div v-if="music.audio.ready && view.animations.cameraIsExecuted && music.audio.status != 'playing'" id="play"
        @click="play()">
        <icon class="white animated pulse infinite" name="play" scale="3.5"></icon>
      </div>
      <div v-if="music.audio.ready && view.animations.cameraIsExecuted && music.audio.status == 'playing'" id="pause"
        @click="play()">
        <icon class="white" name="pause" scale="1.5"></icon>
      </div>
      <div id="fullscreen" @click="setFullScreen()" v-show="!getBrowser().isSafari" v-popover:tooltip.left="'Set fullscreen mode'">
        <icon class="white" :name="view.isFullScreen ? 'compress': 'expand'" scale="2"></icon>
      </div>
      <div v-if="music.audio.status != 'playing'" id="headphones">
        <span id="hint">Turn up volume!</span>
        <icon class="white animated pulse infinite" name="headphones" scale="2"></icon>
      </div>
      <div id="home" @click="goHome()" v-popover:tooltip.right="'Go to songs list'">
        <icon class="white" :name="'arrow-left'" scale="2"></icon>
      </div>
    </div>
    <div v-if="view.isLoading">
      <div class="loaders">
        <div class="loader circle-loader"></div>
      </div>
    </div>
    <div v-if="!view.isLoading" id="streaming" style="bottom: 15px;">
      <span id="hint">Listen on</span>
      <span @click="openLink('spotify')">
        <icon style="margin-left: 8px;" class="white animated pulse infinite" name="brands/spotify" scale="1.5"></icon>
      </span>
      <span @click="openLink('apple-music')">
        <icon style="margin-left: 8px;" class="white animated pulse infinite" name="brands/apple" scale="1.8"></icon>
      </span>
      <span @click="openLink('google-music')">
        <icon style="margin-left: 8px;" class="white animated pulse infinite" name="brands/google-play" scale="1.5"></icon>
      </span>
    </div>
  </div>
</template>

<script>
  import UtilService from "../services/util";
  import InitService from "../services/init";
  import EventService from "../services/event";
  import CreatorService from "../services/creator";
  import AnimatorService from "../services/animator";
  import MusicService from "../services/music";

  export default {
    name: "AlterEgo",
    mixins: [
      UtilService,
      InitService,
      EventService,
      CreatorService,
      AnimatorService,
      MusicService
    ],
    data() {
      var darker = "#4B0D90";
      var lighter = "#99124D";
      var moreLighter = this.colorLuminance(lighter, 0.75);

      return {
        view: {
          camera: null,
          scene: null,
          renderer: null,
          controls: null,
          animations: {
            pulseUp: true,
            pulseDown: false,
            cameraIsExecuted: false
          },
          components: {
            particles: {},
            rocks: {},
            bulb: {},
            grid: {},
            songDetail: {},
            songs: [],
            lights: [],
            ambientLight: {}
          },
          colors: {
            ambient: darker,
            directional: lighter,
            bulb: {
              external: 0xffffff,
              internal: moreLighter
            },
            particle: 0xffffff,
            rocks: moreLighter,
            text: 0xffffff
          },
          font: null,
          isMobile: this.isMobile(),
          isFullScreen: false,
          isVR: false,
          isLoading: true
        },
        music: {
          audio: {
            context: null,
            source: "static/Alter Ego.mp3",
            player: null,
            analyser: null,
            status: null,
            ready: false,
            fftSize: 1024
          },
          title: "Alter Ego",
          artist: "VIT - Edoardo Spadoni",
          position: {
            x: 0,
            y: 3,
            z: 0
          }
        }
      };
    },
    beforeRouteLeave(to, from, next) {
      this.music.audio.player.stop();
      next();
    },
    mounted() {
      this.initView();
    },
    methods: {
      initView() {
        var context = this;

        // add WebGLRenderer
        this.initRenderer();

        // append renderer to view HTML element
        document
          .getElementById("view")
          .appendChild(this.view.renderer.domElement);

        // init the scene
        this.initScene();

        // init camera and add to scene
        this.initCamera({
          y: -5
        });

        // load font
        var loader = new THREE.FontLoader();
        loader.load("static/fonts/open-sans.json", function (font) {
          // add font
          context.view.font = font;

          // init all objects
          context.initAmbient(context);

          // init particles
          context.addParticles(
            true,
            context.music.audio.fftSize / 8,
            context.view.colors.particle,
            false
          );

          // init main bulb
          context.addBulb(
            true,
            false, {
              x: 0,
              y: 0,
              z: 0
            },
            context.view.colors.bulb.internal,
            context.view.colors.bulb.external,
            1,
            1
          );

          // init controls
          context.initControls();

          // init events
          context.initEvents();

          // init audio
          context.initAudio(function (ready) {
            // start animations
            context.animate();

            // terminate loading
            context.view.isLoading = false;
          });
        });
      },

      animate(dt) {
        // request update
        requestAnimationFrame(this.animate);

        // update renderer
        this.updateRenderer();

        // update controls
        this.view.controls.update();

        // animate camera
        if (!this.view.animations.cameraIsExecuted) {
          this.animateCamera();
        }

        if (this.music.audio.status === "playing") {
          // get frequencies for this frame
          var freqs = this.music.audio.analyser.frequencies();

          // animate particles
          this.animateParticles(freqs, true);

          // animate bulb
          this.animateBulb(true, "pulse", freqs, 0.16, 0.0005);
        } else {
          // animate bulb
          this.animateBulb(true, "pulse", null, 0.16, 0.0005);
        }
      }
    }
  };
</script>

<style scoped>
  body {
    background: #4b0d90;
  }
</style>


